import kotlinx.browser.window
import kotlinx.coroutines.MainScope
import kotlinx.coroutines.await
import kotlinx.coroutines.launch
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.h1
import react.setState
import kotlin.random.Random

suspend fun fetchSource(): List<Thirukkural> {
    val sourceTxt =
        window.fetch("https://raw.githubusercontent.com/dreamuth/dreamuth.github.io/master/kural/kurals.txt")
            .await().text().await()
    val thirukurals = readSource(sourceTxt)
    println("All Loaded")
    return thirukurals
}

private fun readSource(sourceTxt: String): MutableList<Thirukkural> {
    val thirukurals = mutableListOf<Thirukkural>()
    val lines = sourceTxt.lines()
    var i = 0
    do {
        val person = lines[i++].trim()
//        println(person)
        val athikaram = lines[i++].split(" - ")
//        println(athikaram)
        i++ // Empty line
        val kural1FirstLine = lines[i++]
        val kural1SecondLine = lines[i++]
        val kural1Only = KuralOnly(kural1FirstLine, kural1SecondLine)
        val kural1Words = getWords("$kural1FirstLine $kural1SecondLine")
        i++ // Empty line
        var kural1Porul = lines[i++]
        while (lines[i].isNotBlank()) {
            kural1Porul += "\n"
            kural1Porul += lines[i++]
        }
        i++ // Empty line
        val kural2FirstLine = lines[i++]
        val kural2SecondLine = lines[i++]
        val kural2Only = KuralOnly(kural2FirstLine, kural2SecondLine)
        val kural2Words = getWords("$kural2FirstLine $kural2SecondLine")
        i++ // Empty line
        var kural2Porul = lines[i++]
        while (!lines[i].startsWith("----")) {
            kural2Porul += "\n"
            kural2Porul += lines[i++]
        }
        i++ // Empty line
        val thirukural1 = Thirukkural(athikaram[0], athikaram[1], kural1Only, kural1Porul, kural1Words, person)
        val thirukural2 = Thirukkural(athikaram[0], athikaram[1], kural2Only, kural2Porul, kural2Words, person)
        thirukurals.add(thirukural1)
        thirukurals.add(thirukural2)
    } while (i < lines.size && i + 5 < lines.size)
    return thirukurals
}

enum class Topic(val tamil: String) {
    Athikaram("அதிகாரம்"),
    AthikaramPorul("அதிகாரம் பொருள்"),
    KuralSol("திருக்குறள் சொல்"),
    KuralPorul("திருக்குறள் பொருள்"),
    KuralEluthu("திருக்குறள் எழுத்து"),
    Statistics("புள்ளி விவரம்")
}

external interface AppState: RState {
    var loaded: Boolean
    var selectedTopic: Topic
    var selectedPerson: String
    var athikaramKeyList: List<String>
    var activeAthikaramKeyList: List<String>
    var thirukkurals: List<Thirukkural>
    var activeThirukkurals: List<Thirukkural>
    var words: List<String>
    var activeWords: List<String>
    var persons: List<String>
    var athikaramIndex: Int
    var kuralPorulIndex: Int
    var wordIndex: Int
    var firstLineWordsSrc: List<List<String>>
    var secondLineWordsSrc: List<List<String>>
    var firstLineWordsTarget: List<MutableList<String>>
    var secondLineWordsTarget: List<MutableList<String>>
    var noOfAttempts: Int
    var showAnswer: Boolean
}

class App : RComponent<RProps, AppState>() {
    override fun AppState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            val result = fetchSource()
            setState {
                thirukkurals = result
                selectedTopic = Topic.Athikaram
                athikaramKeyList = result.map { it.athikaram }.distinct()
                words = thirukkurals.map { it.words }.flatten().distinct()
                persons = listOf("அனைவரும்") + thirukkurals.map { it.person }.distinct()
                activeAthikaramKeyList = athikaramKeyList
                activeThirukkurals = thirukkurals
                activeWords = words
                selectedPerson = persons[0]
                athikaramIndex = nextIndex(athikaramIndex, activeAthikaramKeyList.size)
                kuralPorulIndex = nextIndex(kuralPorulIndex, activeThirukkurals.size)
                wordIndex = nextIndex(wordIndex, activeWords.size)
                firstLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.take(4).map { getCharList(it).toList() }
                secondLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { getCharList(it).toList() }
                firstLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.take(4).map { MutableList(getCharList(it).size) { "_" } }
                secondLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { MutableList(getCharList(it).size) { "_" } }
                noOfAttempts = 0
                loaded = true
                println(athikaramKeyList)
                println("No of athikarams: ${athikaramKeyList.size}")
                println("No of kurals: ${thirukkurals.size}")
                println("No of unique words from all kurals: ${words.size}")
            }
        }
    }

    override fun RBuilder.render() {
        h1 {
            +"திருக்குறள் பயிற்சி"
        }
        if (state.loaded) {
            person {
                thirukkurals = state.thirukkurals
                athikaramKeyList = state.athikaramKeyList
                words = state.words
                persons = state.persons
                showAnswer = state.showAnswer
                selectedTopic = state.selectedTopic
                selectedPerson = state.selectedPerson
                activeThirukkurals = state.activeThirukkurals
                activeAthikaramKeyList = state.activeAthikaramKeyList
                activeWords = state.activeWords
                firstLineWordsSrc = state.firstLineWordsSrc
                firstLineWordsTarget = state.firstLineWordsTarget
                secondLineWordsSrc = state.secondLineWordsSrc
                secondLineWordsTarget = state.secondLineWordsTarget
                athikaramIndex = state.athikaramIndex
                kuralPorulIndex = state.kuralPorulIndex
                wordIndex = state.wordIndex
                noOfAttempts = state.noOfAttempts
                onShowAnswerClick = { show ->
                    setState {
                        showAnswer = show
                        if (selectedTopic == Topic.KuralEluthu) {
                            firstLineWordsTarget = firstLineWordsSrc.map { it.toMutableList() }
                            secondLineWordsTarget = secondLineWordsSrc.map { it.toMutableList() }
                        }
                    }
                }
                onPersonClick = { personName ->
                    setState {
                        if (state.selectedPerson != personName) {
                            showAnswer = false
                            selectedPerson = personName
                            if (selectedPerson == "அனைவரும்") {
                                activeThirukkurals = thirukkurals
                                activeAthikaramKeyList = athikaramKeyList
                                activeWords = words
                            } else {
                                activeThirukkurals = thirukkurals.filter { it.person == personName }
                                activeAthikaramKeyList = activeThirukkurals.map { it.athikaram }.distinct()
                                activeWords = activeThirukkurals.map { it.words }.flatten().distinct()
                            }
                            athikaramIndex = nextIndex(athikaramIndex, activeAthikaramKeyList.size)
                            kuralPorulIndex = nextIndex(kuralPorulIndex, activeThirukkurals.size)
                            wordIndex = nextIndex(wordIndex, activeWords.size)
                            firstLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.take(4).map { getCharList(it).toList() }
                            secondLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { getCharList(it).toList() }
                            firstLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.take(4).map { MutableList(getCharList(it).size) { "_" } }
                            secondLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { MutableList(getCharList(it).size) { "_" } }
                            noOfAttempts = 0
                        }
                    }
                }
                onTopicClick = { topic ->
                    setState {
                        if (selectedTopic != topic) {
                            showAnswer = false
                            selectedTopic = topic
                        }
                    }
                }
                onNextClick = {
                    when (selectedTopic) {
                        Topic.Athikaram, Topic.AthikaramPorul -> {
                            setState {
                                athikaramIndex = nextIndex(athikaramIndex, activeAthikaramKeyList.size)
                            }
                        }
                        Topic.KuralPorul -> {
                            setState {
                                kuralPorulIndex = nextIndex(kuralPorulIndex, activeThirukkurals.size)
                            }
                        }
                        Topic.KuralSol -> {
                            setState {
                                wordIndex = nextIndex(wordIndex, activeWords.size)
                            }
                        }
                        Topic.Statistics -> {
                        }
                        Topic.KuralEluthu -> {
                            setState {
                                kuralPorulIndex = nextIndex(kuralPorulIndex, activeThirukkurals.size)
                                firstLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.take(4).map { getCharList(it).toList() }
                                secondLineWordsSrc = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { getCharList(it).toList() }
                                firstLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.take(4).map { MutableList(getCharList(it).size) { "_" } }
                                secondLineWordsTarget = activeThirukkurals[kuralPorulIndex].words.takeLast(3).map { MutableList(getCharList(it).size) { "_" } }
                                noOfAttempts = 0
                            }
                        }
                    }
                }
                onLetterClick = { letter ->
                    checkLetter(letter, state.firstLineWordsSrc, state.firstLineWordsTarget)
                    checkLetter(letter, state.secondLineWordsSrc, state.secondLineWordsTarget)
                    setState {
                        noOfAttempts++
                    }
                }
            }
        }
    }
    private fun checkLetter(letter: String, sourceList: List<List<String>>, targetLine: List<MutableList<String>>) {
        for (i in sourceList.indices) {
//            println("i = $i")
//            println(sourceList[i])
            val wordList = sourceList[i]
            for (j in wordList.indices) {
//                println("j = $j")
//                println(wordList[j])
                val keyLetter = getKeyLetter(wordList[j])
                if (letter == keyLetter) {
//                    println("Letter found $letter")
                    setState {
                        targetLine[i][j] = wordList[j]
                    }
                }
            }
        }
    }

    private fun getKeyLetter(char: String) =
        keyLetters[char] ?: error("Missing $char ${char.toCharArray()[0].toInt()}")

    private fun nextIndex(currentIndex: Int, maxIndex: Int): Int {
        var newIndex: Int
        do {
            newIndex = Random.nextInt(maxIndex)
        } while (newIndex == currentIndex)
        println("Current: $currentIndex to New: $newIndex of Total: $maxIndex")
        return newIndex
    }
}

private fun getWords(line: String): List<String> {
    val list = mutableListOf<String>()
    var currentWord = ""
    val charArray = line.toCharArray()
    for (char in charArray) {
//        println("Reading $char ${char.toInt()}")
        when (char.toInt()) {
            // Space, Dot, Zero-width non-joiner
            32, 39, 44, 46, 58, 8204 -> {
                if (currentWord.isNotBlank()) {
                    list.add(currentWord)
                }
                currentWord = ""
            }
            else -> {
                currentWord += char
            }
        }
    }
    return list
}
