import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement

external interface SolProps: RProps {
    var question: String
    var selectedThirukkurals: List<Thirukkural>
    var showAnswer: Boolean
}

class Sol : RComponent<SolProps, RState>() {
    override fun RBuilder.render() {
        question {
            question = props.question
        }
        if (props.showAnswer) {
            props.selectedThirukkurals.forEach { thirukkural ->
                kuralDisplay {
                    selectedThirukkural = thirukkural
                }
            }
        }
    }
}

fun RBuilder.sol(handler: SolProps.() -> Unit): ReactElement {
    return child(Sol::class) {
        this.attrs(handler)
    }
}
