import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import react.dom.h2
import react.dom.h3
import react.setState
import styled.styledDiv

external interface StatisticsDisplayProps: RProps {
    var thirukkurals: List<Thirukkural>
}

external interface StatisticsDisplayState: RState {
    var loaded: Boolean
    var topLettersInAll: Map<String, Int>
    var firstLineTopFirstLetters: Map<String, Int>
    var secondLineTopFirstLetters: Map<String, Int>
}

class StatisticsDisplay : RComponent<StatisticsDisplayProps, StatisticsDisplayState>() {
    override fun StatisticsDisplayState.init() {
        val mainScope = MainScope()
        mainScope.launch {
            setState {
                val kurals = props.thirukkurals.map { it.kural }.toList()
                topLettersInAll = getTopLettersInAll(kurals, keyLetters)
                firstLineTopFirstLetters = getTopLettersInFirstLineFirstWord(kurals, keyLetters)
                secondLineTopFirstLetters = getTopLettersInSecondLineFirstWord(kurals, keyLetters)
                loaded = true
            }
        }
    }

    override fun RBuilder.render() {
        if (state.loaded) {
            styledDiv {
                h2 {
                    +"அதிகமாக உபயோகப்படுத்திய எழுத்துக்கள்"
                }
                h3 {
                    state.topLettersInAll.forEach { +"${it.key}:${it.value}   " }
                }
                h2 {
                    +"முதல் வரி முதல் வார்த்தையில் அதிகமாக உபயோகப்படுத்திய எழுத்துக்கள்"
                }
                h3 {
                    state.firstLineTopFirstLetters.forEach { +"${it.key}:${it.value}   " }
                }
                h2 {
                    +"இரண்டாம் வரி முதல் வார்த்தையில் அதிகமாக உபயோகப்படுத்திய எழுத்துக்கள்"
                }
                h3 {
                    state.secondLineTopFirstLetters.forEach { +"${it.key}:${it.value}   " }
                }
            }
        }
    }
}

fun RBuilder.statisticsDisplay(handler: StatisticsDisplayProps.() -> Unit): ReactElement {
    return child(StatisticsDisplay::class) {
        this.attrs(handler)
    }
}

fun getTopLettersInFirstLineFirstWord(kurals: List<KuralOnly>, keyLetters: Map<String, String>): Map<String, Int> {
    val result = mutableMapOf<String, Int>()
    val allCharsList = kurals.map { it.firstLine }
        .map { it.toCharArray() }
        .map { getFirstLetter(it) }
        .toList()
    allCharsList.forEach {
        val keyLetter = keyLetters[it] ?: error("Missing $it ${it.toCharArray()[0].toInt()}")
        val initialValue = result.getOrPut(keyLetter, { 0 })
        result[keyLetter] = initialValue + 1
    }
    return result.toList().sortedByDescending { (_, value) -> value }.toMap()
}

private fun getFirstLetter(it: CharArray): String {
    var result: String = it[0].toString()
    if (it[1].toInt() in 3006..3021) {
        result += it[1]
    }
    return result
}

fun getTopLettersInSecondLineFirstWord(kurals: List<KuralOnly>, keyLetters: Map<String, String>): Map<String, Int> {
    val result = mutableMapOf<String, Int>()
    val allCharsList = kurals.map { it.secondLine }
        .map { it.toCharArray() }
        .map { getFirstLetter(it) }
        .toList()
    allCharsList.forEach {
        val keyLetter = keyLetters[it] ?: error("Missing $it ${it.toCharArray()[0].toInt()}")
        val initialValue = result.getOrPut(keyLetter, { 0 })
        result[keyLetter] = initialValue + 1
    }
    return result.toList().sortedByDescending { (_, value) -> value }.toMap()
}

private fun getTopLettersInAll(kurals: List<KuralOnly>, keyLetters: Map<String, String>): Map<String, Int> {
    val result = mutableMapOf<String, Int>()
    val allCharsList = getCharList(kurals)
    allCharsList.forEach {
        val keyLetter = keyLetters[it] ?: error("Missing $it ${it.toCharArray()[0].toInt()}")
        val initialValue = result.getOrPut(keyLetter, { 0 })
        result[keyLetter] = initialValue + 1
    }
    return result.toList().sortedByDescending { (_, value) -> value }.toMap()
}

private fun getCharList(kurals: List<KuralOnly>): List<String> {
    return kurals.map { it.firstLine + it.secondLine }
        .map { getCharList(it) }
        .toList()
        .flatMap { it.toList() }
}

fun getCharList(line: String): MutableList<String> {
    val list = mutableListOf<String>()
    val charArray = line.toCharArray()
    for (char in charArray) {
        when (char.toInt()) {
            // Space, Dot
            32, 39, 44, 46, 58, 8204 -> {
                // do nothing
            }
            in 3006..3021 -> {
                list[list.size - 1] = list[list.size - 1] + char
            }
            else -> {
                list.add(char.toString())
            }
        }
    }
    return list
}
