import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement

external interface AthikaramPorulProps: RProps {
    var selectedThirukkurals: List<Thirukkural>
    var showAnswer: Boolean
}

class AthikaramPorul : RComponent<AthikaramPorulProps, RState>() {
    override fun RBuilder.render() {
        question {
            question = props.selectedThirukkurals[0].athikaramDesc
        }
        if (props.showAnswer) {
            props.selectedThirukkurals.forEach { thirukkural ->
                kuralDisplay {
                    selectedThirukkural = thirukkural
                }
            }
        }
    }
}

fun RBuilder.athikaramPorul(handler: AthikaramPorulProps.() -> Unit): ReactElement {
    return child(AthikaramPorul::class) {
        this.attrs(handler)
    }
}
